var normalizeList = function (list) {
    var result = [
        {
            title: '',
            list: []
        },
        {
            title: '',
            list: []
        }
    ];
    var myList = [];
    var otherList = [];
    list.forEach(function (element) {
        // 
        if (element.creator.defaultAvatar) {
            result[0].list.push(element);
        }
        else {
            result[1].list.push(element);
        }
    });
    return result;
};
export default normalizeList;



